import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			src:'../../static/nogoods.png',
			msg:'你还没有任何订单，看看其他的吧',
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 4,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
			],
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */

		this.tabCurrentIndex = +options.state;
		this.loadData();
		if (options.state == 0) {
			this.loadData()
		}
	},

	methods: {
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}

			// navItem.loadingType = 'loading';

			let orderList = Json.orderList.filter(item => {
				//添加不同状态下订单的表现形式
				item = Object.assign(item, this.orderStateExp(item.state));
				//演示数据所以自己进行状态筛选
				if (state === 0) {
					//0为全部订单
					return item;
				}
				return item.state === state
			});
			orderList.forEach(item => {
				navItem.orderList.push(item);
			})
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			this.$set(navItem, 'loaded', true);

			//判断是否还有数据， 有改为 more， 没有改为noMore 
			// navItem.loadingType = 'more';

		},

		//swiper 切换
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		// 评价
		evaluate(item) {
			console.log(item)
		},
		// 再次购买
		againBuy(item) {
			console.log(item)
		},
		//取消订单
		cancelOrder(item) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				let {
					stateTip,
					stateTipColor
				} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip,
					stateTipColor
				})

				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val => val.id === item.id);
				index !== -1 && list.splice(index, 1);

				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待支付';
					break;
				case 4:
					stateTip = '待发货';
					break;
				case 2:
					stateTip = '待收货';
					break;
				case 3:
					stateTip = '待评价';
					break;
				case 5:
					stateTip = '';
					break;
				case 6:
					stateTip = '交易成功';
					break;
				case 7:
					stateTip = '已完成';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;
			}
			return {
				stateTip,
				stateTipColor
			};
		},
		//订单详情
		orderDetails(item) {
			if(item.state!=3){
				uni.navigateTo({
					url: 'orderDetails?status=' + item.state
				})
			}
			
		},
		// 物流信息
		logisticsTap(){
			uni.navigateTo({
				url:'logistics'
			})
		}
	},
}
